document.addEventListener('DOMContentLoaded', function() {
  eventListeners();
})
var countVisible = 0;
var palabra = "";
var palabras = [];
var palabrasDic = {"JHON ROBERT":"JHON-ROBERT", "FOR YOU": "FOR-YOU"};
var palabrasKeys = ["JHON ROBERT", "FOR YOU"];
function eventListeners() {
  const barra = document.querySelector(".barra");
  const letra = document.querySelectorAll(".f");
  letra.forEach( l=> {
    l.addEventListener("mouseover", e=> {
      e.preventDefault();
      palabra = l.textContent;
      if (palabrasKeys.includes(palabra)) {
        palabra = palabrasDic[palabra];
      }
      const algo = document.querySelector(`.${palabra}`);
      algo.classList.add('visible');
      if (!palabras.includes(palabra)) {
        
        palabras.push(palabra);
      }
      if (palabras.length === 5 ) {
        document.querySelector('.PROGRAMER').classList.add('colores');
        document.querySelector('.view-work').classList.add('colores');
      }
    });
    

  })
  const menuDisplay = ["JHON-ROBERT", "PROGRAMER", "NOW", "AVAILABLE", "FOR-YOU"];
    menuDisplay.forEach( mD => {
      const menuWord = document.querySelector(`.${mD}`);
      window.getComputedStyle(menuWord).getPropertyValue('visibility') === 'visible' ? countVisible +=1 : countVisible = countVisible;
      console.log(countVisible);
    })
    if (countVisible === 5 ) {
      document.querySelector('.PROGRAMER').classList.add('colores');
      document.querySelector('.view-work').classList.add('colores');
    }
  barra.addEventListener('click', mostrarMenu);
}

function mostrarMenu() {
  const menu = document.querySelector('.bg_animate');

  menu.classList.toggle('menu');
}


var cuadrado = document.querySelector('.contenedor-resumen h2');
cuadrado.classList.add('mostrar-resumen');